<template>
    <Form :model="formItem" :label-width="80">
        <FormItem>
            <Upload v-on:upload-img="formItem.cover=$event"></Upload>
        </FormItem>
        <FormItem label="">
            <Input v-model="formItem.placeDetail" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
            <Input v-model="formItem.content" type="textarea" :autosize="{minRows:5}" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
            <Select v-model="formItem.placeId">
                <Option value="1"></Option>
                <Option value="2"></Option>
                <Option value="3"></Option>
                <Option value="4"></Option>
                <Option value="5"></Option>
                <Option value="6"></Option>
                <Option value="7"></Option>
            </Select>
        </FormItem>
        <FormItem label="">
            <Input v-model="formItem.authorName" placeholder=""></Input>
        </FormItem>
        <FormItem label="">
            <Select v-model="formItem.authorSchool">
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
            </Select>
        </FormItem>
        <FormItem label="">
            <DatePicker v-model="formItem.authorLevel" type="year" placeholder="2014 " style="width: 200px"></DatePicker>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="submitPost()"></Button>
            <Button type="ghost" style="margin-left: 8px" @click="$router.go(-1)"></Button>
        </FormItem>
    </Form>
</template>
<script>
import Upload from "./Upload.vue";
export default {
    components: {
        Upload
    },
    data() {
        return {
            formItem: {
                covor: "",
                placeDetail: "",
                content: "",
                authorName: "",
                authorSchool: "",
                authorLevel: "",
                placeId: ""
            }
        };
    },
    methods: {
        submitPost: function() {
            if (!this.isValid) {
                this.$Message.info("");
                return;
            }
            this.$axios
                .post("/api/post", this.formItem)
                .then(response => {
                    if (response.data.status) {
                        this.$Message.success(response.data.msg);
                        const pid = this.formItem.placeId;
                        this.$router.push({ name: "place", params: { pid } });
                        return;
                    }
                })
                .catch(function(error) {
                    console.error(error);
                });
        }
    },
    computed: {
        isValid: function() {
            return (
                this.formItem.cover != "" &&
                this.formItem.placeId != "" &&
                this.formItem.placeDetail != "" &&
                this.formItem.content != "" &&
                this.formItem.authorName != "" &&
                this.formItem.authorSchool != "" &&
                this.formItem.authorLevel != ""
            );
        }
    }
};
</script>
